@font-face {
  font-family: 'Digital';
  src: url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot');
  src: url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot?#iefix')
      format('embedded-opentype'),
    url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff2')
      format('woff2'),
    url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff')
      format('woff'),
    url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.ttf')
      format('truetype'),
    url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.svg#Digital-7')
      format('svg');
}

body{
  background-color: grey;
  display: flex;
  justify-content: center;
}
#calculator{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  height: 497px;
  width: 329px;
  margin-top: 5px;
  background-color: black;
  padding: 5px 5px 5px 5px;
}
#display{
  width: 326px;
  height: 30px;
  background-color: black;
  color: white;
  border: 1px solid white;
  display: flex;
  justify-content: flex-end;
  padding: 5px 10px 5px 10px;
  margin-bottom: 5px;
  font-family: 'Digital';
  font-size: 26px;
  border-top: none;
}
#output{
  width: 326px;
  height: 30px;
  background-color: black;
  color: darkorange;
  border: 1px solid white;
  display: flex;
  justify-content: flex-end;
  padding: 5px 10px 5px 10px;
  font-family: 'Digital';
  font-size: 20px;
  border-bottom: none;
}
.num{
  background-color: #4d4d4d;
  border: 1px solid black;
  width: 80px;
  height: 80px;
  font-size: 25px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.num:hover {
    color: black;
    outline: 0.05em solid grey;
  }
#clear{
  width: 162px;
  background-color: darkred;
}
#zero{
  width: 162px;
}
#blank, #equals{
  background-color: darkblue;
}
#input{
  color: white;
}
#divide, #multiply, #add, #subtract {
  background-color: grey;
}
#blank{
  pointer-events: none;
  
}

